import React from 'react';
import {useDispatch, useSelector} from "react-redux";
import {addCustomerAction, deleteCustomerAction, removeCustomerAction} from "./store/customerReducer";

function Bank(props) {
    //  
    const dispatch = useDispatch();
    // 
    const cash = useSelector(state => state.cash.cash);
    const customers = useSelector(state => state.customers.customers);

    function addCash(cash) {
        dispatch({type: 'add_cash', payload: cash});
    }

    function getCash(cash) {
        dispatch({type: 'get_cash', payload: cash});

    }

    function addCustomer(name) {
        const customer = {
            name: name,
            id: Date.now(),
        };
        // dispatch({type: 'add_customer', payload: customer})
        dispatch(addCustomerAction(customer));
    }

    function removeCustomer(customer) {
// dispatch({type:'remove_customer', payload:customer.id})
        dispatch(removeCustomerAction(customer.id));
    }

    function deleteCustomer(name) {
        dispatch(deleteCustomerAction(name));

    }

    return (
        <div className={'app'}>
            <div>{cash}</div>
            <div style={{display: 'flex'}}>
                <button onClick={() => addCash(Number(prompt()))}> </button>
                <button onClick={() => getCash(Number(prompt()))}>  </button>
                <button onClick={() => addCustomer(prompt())}> </button>
                <button onClick={() => deleteCustomer(prompt())}> </button>

            </div>
            <div>

                {
                    customers.length > 0 ?
                        customers.map(item =>
                            <div onClick={() => removeCustomer(item)}>
                                {item.name}</div>
                        ) : <div> </div>
                }

            </div>

        </div>
    );
}

export default Bank;